$drum-main-bg-clr: #161616;
$display-main-bg-clr: #0e0d0d;
$btn-clr-main-bg-clr: #2b2b2b;
#drum-machine{
  background-color: $drum-main-bg-clr;
  width: 400px;
  height: 540px;
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: center;
  border: 1px solid black;
  box-shadow: 0px 0px 6px 3px rgb(12, 12, 12) inset;
}
.top-block{
  display: flex;
  flex-direction: column;
  padding-top: 50px;
  justify-content: center;
  align-items: center;
}
#display{
  width: 320px;
  height: 160px;
  background-color: $display-main-bg-clr;
  box-shadow: 0px 1px 6px 1px black;
  border: 2px solid rgb(32, 32, 32);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  p{
    color: rgb(255, 255, 255);
    font-size: 20px;
    font-family: Verdana, Geneva, Tahoma, sans-serif
  }
}
#drum-pad-container{
  min-width: 300px;
  max-width: 310px;
  margin-top: 30px;
  margin-left: 60px;
  max-height: 220px;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  .drum-pad{
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 0px 5px 7px 5px;
    border-radius: 10px;
    border: 2px solid rgb(10, 10, 10);
    background-color: $btn-clr-main-bg-clr;
    box-shadow: 0px -6px 1px 4px rgb(19, 18, 18) inset;
    font-size: 1.2em;
    font-family: Arial, Helvetica, sans-serif;
    color: rgb(255, 255, 255);
    height: 74px;
    width: 74px;
  }
 .drum-pad:focus{
  outline: none;
  background-color: rgb(54, 54, 54);
 }
}

.right-panel{
  // background-color: rgb(24, 24, 24);
  width: 200px;
  margin-top: 20px;
  flex-wrap: nowrap;
  align-items: center;
  justify-content: center;
  display: flex;
  .volume-bar{
    -webkit-appearance: none;  /* Override default CSS styles */
    appearance: none;
    width: 100%; /* Full-width */
    height: 10px; /* Specified height */
    border-radius: 3px;
    background: #0e0e0e; /* Grey background */
    box-shadow: 0px 1px 4px 2px rgba(0, 0, 0, 0.692) inset;
    opacity: 0.7; /* Set transparency (for mouse-over effects on hover) */
    -webkit-transition: .2s; /* 0.2 seconds transition on hover */
    transition: opacity .2s;
  }
  .volume-bar:focus{
    outline: none;
  }
  .volume-bar::-webkit-slider-thumb{
    -webkit-appearance: none; /* Override default look */
    width: 14px; /* Set a specific slider handle width */
    height: 34px; /* Slider handle height */
    background: #1b1b1b; /* Green background */
    box-shadow: 0px 0px 2px 2px rgba(0, 0, 0, 0.733) inset;
    border-radius: 3px;
    cursor: pointer; /* Cursor on hover */
  } 
}

.play-anim{
  animation-name: drum-pad-anim;
  animation-duration: 0.16s;
  animation-iteration-count: 1;
  animation-delay: 0s;
  animation-direction: normal;
}

@keyframes drum-pad-anim {
  100%{
    background-color: rgb(82, 82, 82);
    box-shadow: 0px 0px 1px 0px rgb(22, 22, 22) inset;
  }
}
